import Vue from 'vue'
import Vuex from 'vuex'
import itemModule from './item.module'



Vue.use(Vuex)

export default new Vuex.Store({
  strict: true,
  state: {
    userMsg: '',
    cart: [],
    images: []
  },
  getters: {
    userMsg(state) {
      return state.userMsg
    },
    total(state) {
      if (state.cart.length > 0) {
        return state.cart.map(item => item.price).reduce((total, amount) => total + amount);
      } else {
        return 0;
      }
    },

    imgs(state) {
      return state.images
    }
  },
  mutations: {
    setMsg(state, { msgStr }) {
      state.userMsg = msgStr
    },
    addToCart(state, payload) {
      return state.cart.push(payload);
    },
    imgCarousel(state, { imgs }) {
      state.images = imgs

    },
  },
  actions: {
    setMsg({ commit }, { msgStr }) {
      commit({ type: 'setMsg', msgStr })
      setTimeout(() => {
        commit({ type: 'setMsg', msgStr: '' })
      }, 3000)
    }
  },
  modules: {
    itemModule
  }
})
