async function query() {
    console.log(itemsDB)
    var items = await storageService.load(ITEMS_KEY)
    if (!items || !items.length) {
        items = itemsDB
        storageService.save(ITEMS_KEY, items)
    }
    return items
}

async function remove(id) {
    return DbService.delete(ITEMS_KEY, id)
}
async function setItemId(currId, diff) {
    let items = await Utils.loadFromStorage(ITEMS_KEY)
    let currItemIdx = items.findIndex(item => {
        return item.id === currId
    })
    let nextItemId = items[currItemIdx + diff].id
    return nextItemId
}
async function getById(itemId) {
    var theItem = await itemsDB.find(item => item.id === itemId)
    console.log('this', itemId)
    return Promise.resolve(theItem)
}

function get(id) {
    return DbService.get(ITEMS_KEY, id)
}
