import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import '@/style/global.scss'

Vue.config.productionTip = false


Vue.directive('rainbow', {
  inserted(el) {
    el.style.color = '#' + Math.random().toString().slice(3, 9)
  }
})

Vue.directive('focus', {
  inserted(el) {
    el.focus()
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
