<template>
  <section>
    <div class="nav-btns">
      <button class="back-btn" @click="onBack">Back to List</button>
      <div>
        <button class="prev-btn" @click="changeItem(-1)">Previous Item</button>
        <button class="next-btn" @click="changeItem(1)">Next Item</button>
      </div>
    </div>
    <main v-if="item" class="item-details">
      <div class="item-main">
        <img :src="item.itemImg" alt="" class="item-img" />
        <div class="item-head">
          <h1>{{ item.title }}</h1>
          <p>Rating {{ item.rating }}</p>
          <p>{{ item.desc }}</p>
          <div>
            <div>
              <transition name="modal">
                <div v-if="isOpen">
                  <div class="overlay" @click.self="isOpen = false">
                    <div class="modal">
                      <router-link :to="`/thankyou/${item.title}`">
                        <button @click="makePdf()">Checkout!</button>
                      </router-link>
                    </div>
                  </div>
                </div>
              </transition>
              <button @click="isOpen = !isOpen">Buy</button>
            </div>
            <p>$ {{ item.price }}</p>
          </div>
        </div>
      </div>
      <h2>Gameplay Video and Images</h2>
      <div class="item-view">
        <iframe
          width="560"
          height="200"
          :src="`https://www.youtube.com/embed/${item.video}`"
          frameborder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
          class="game-video"
        ></iframe>
        <div class="gameplay-gallery">
          <div>
            <div>
              <button class="left-arrow" @click="prev">â¬…ï¸</button>
              <button class="right-arrow" @click="next">âž¡ï¸</button>
            </div>
            <img
              :src="item.gameplay[currentNumber % item.gameplay.length]"
              alt="gameplay"
              class="item-gameplay"
            />
          </div>
        </div>
      </div>
    </main>
    <img v-else src="../assets/img/loading.svg" />
  </section>
</template>

<script>
import { itemService } from "../services/itemService";
import eventBus from "@/services/eventBus.service.js";
import { jsPDF } from "jspdf";

export default {
  data() {
    return {
      items: null,
      item: null,
      isOpen: false,
      currentNumber: 0,
    };
  },
  methods: {
    next: function () {
      this.currentNumber += 1;
    },
    prev: function () {
      this.currentNumber -= 1;
    },
    makePdf() {
      let pdfName = "test";
      var doc = new jsPDF();
      doc.text(
        `${this.item.title}  $${this.item.price} ` +
          new Date().toLocaleString(),
        10,
        10
      );
      doc.save(pdfName + ".pdf");
    },

    async loadItems() {
      this.items = await itemService.query();
      console.log(this.items);
    },
    async loadItem() {
      const itemId = this.$route.params.itemId;
      await itemService.getById(itemId).then((item) => (this.item = item));
    },
    async changeItem(diff) {
      const { id } = this.item;
      setTimeout(async () => {
        let nextBookId = await itemService.setItemId(id, diff);
        this.$router.push("/item/" + nextBookId);
      }, 3000);
    },
    stopRotation: function () {
      clearTimeout(this.timer);
      this.timer = null;
    },

    onBack() {
      eventBus.$emit("visited", this.item.title);
      this.$store.dispatch({ type: "setMsg", msgStr: this.item.title });
      this.$router.push("/items");
    },

    currentImage() {
      console.log(this.item.gameplay);
      this.imgs = this.item.gameplay;
      return (this.imgs = this.images);
    },
  },
  computed: {
    nextItemLink() {
      return "/item/" + this.nextItemId;
    },
  },
  created() {
    const { itemId } = this.$route.params;
    setTimeout(async () => {
      this.item = await itemService.get(itemId);
      console.log("item", this.item);
    }, 1000);
  },

  watch: {
    "$route.params.itemId"(id) {
      console.log(id);
      this.loadItem();
    },
  },
  components: {},
};
</script>
<style scoped>
.modal {
  width: 500px;
  margin: 0px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px 3px;
  transition: all 0.2s ease-in;
  font-family: Helvetica, Arial, sans-serif;
}
.fadeIn-enter {
  opacity: 0;
}

.fadeIn-leave-active {
  opacity: 0;
  transition: all 0.2s step-end;
}

.fadeIn-enter .modal,
.fadeIn-leave-active.modal {
  transform: scale(1.1);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: #00000094;
  z-index: 999;
  transition: opacity 0.2s ease;
}
</style>