<template>
  <section class="item-preview">
    <div>
      <h3>
        {{ item.title }}
      </h3>
      <router-link :to="`/item/${item.id}`">
        <img :src="item.itemImg" :alt="item.title" class="item-img-list"
      /></router-link>
    </div>
    <div class="item-actions">
      <p>$ {{ item.price }}</p>
      <!-- <button @click="remove">Delete</button> -->
      <button class="cta-btn">Buy</button>
      <button @click="addToCart(item)">Add To Cart</button>
    </div>
  </section>
</template>
<script>
export default {
  props: ["item"],
  data() {
    return {};
  },
  methods: {
    remove() {
      this.$emit("remove", this.item.id);
    },
    addToCart(item) {
      {
        this.$store.commit({
          type: "addToCart",
          // id: item.id,
          item: item.title,
          price: item.price,
        });
      }
    },
  },
};
</script>
