<template>
  <div id="ShoppingCart">
    <p>Your Cart</p>
    <p>No. of items: {{ cart.length }}</p>
    <p>{{ title }}</p>
    <p>Total: ${{ total }}</p>
  </div>
</template>

<script>
export default {
  components: {},
  props: ["cart", "total", "title"],
  methods: {
    itemName() {
      this.$store.dispatch({ type: "itemName", name: this.item.title });
    },
  },
  computed: {},
};
</script>

<style>
p {
  margin: 0;
}

#ShoppingCart {
  padding: 10px;
  border-radius: 5px;
  background: #494949;
  width: 130px;
  position: relative;
  margin: 0 auto;
}
</style>
