import { itemService } from "../services/itemService";

export default {
    state: {
        items: null,
        filterBy: null,
    },
    getters: {
        itemsToShow(state) {
            const { items } = state;
            if (!state.filterBy) return items;
            const regex = new RegExp(state.filterBy.txt, 'i');
            console.log('res', regex)
            const itemsToShow = items.filter((item) => regex.test(item.title));
            return itemsToShow;
        },
    },

    mutations: {
        setItems(state, payload) {
            state.items = payload.items
        },
        setFilter(state, { filterBy }) {
            state.filterBy = filterBy
        },
    },
    actions: {
        async loadItems({ commit }) {
            const items = await itemService.query()
            commit({ type: 'setItems', items })
        }
    },
}
