<template>
  <section>
    <ul class="item-list">
      <item-preview
        v-for="item in items"
        @remove="remove"
        :key="item.id"
        :item="item"
      />
    </ul>
  </section>
</template>

<script>
import ItemPreview from "../cmps/ItemPreview";

export default {
  props: ["items"],
  methods: {
    remove(itemId) {
      this.$emit("remove", itemId);
    },
  },
  components: {
    ItemPreview,
   
  },
};
</script>
