<template>
  <section>
    <cart :cart="shoppingCart" :total="totalAmount" />
    <item-filter
      v-if="items"
      @filtered="setFilter"
      :genresForDisplay="genresForDisplay"
    />
    <item-details :item="item" v-if="selectedItem" />
    <item-list :items="itemsToShow" @remove="removeItem"> </item-list>
  </section>
</template>

<script>
import { itemService } from "../services/itemService";
import ItemList from "../cmps/ItemList.vue";
import ItemFilter from "../cmps/ItemFilter.vue";
import ItemDetails from "../views/ItemDetails";
import Cart from "../cmps/Cart";
export default {
  // props:['items'],
  data() {
    return {
      items: null,
      selectedItem: null,
      filterBy: null,
    };
  },
  methods: {
    async loadItems() {
      this.items = await itemService.query();
      //  await this.$store.dispatch({ type: "loadItems" });
      console.log(this.items);
    },
    selectItem(item) {
      this.selectedItem = item;
    },
    async removeItem(itemId) {
      await itemService.remove(itemId);
      this.loadItems();
    },
    setFilter(filterBy) {
      this.$store.commit({ type: "setFilter", filterBy });
    },
  },
  async created() {
    this.$store.dispatch({ type: "loadItems" });
    this.loadItems();
  },
  computed: {
    itemsToShow() {
      return this.$store.getters.itemsToShow;
    },
    genresForDisplay() {
      const genres = this.items.map((item) => item.genre);
      console.log(genres);
      return new Set(genres);
    },
    shoppingCart() {
      return this.$store.state.cart;
    },
    totalAmount() {
      return this.$store.getters.total;
    },
  },
  components: { ItemList, ItemDetails, ItemFilter, Cart },
};
</script>